import { Parallax as ParallaxComponent, Background } from "react-parallax";

import backgroundImage from '../../assets/images/photo.jpg'

import './parallax.css'

const Parallax = () => {
  return (
    <ParallaxComponent className="parallax" bgImage={backgroundImage} strength={150}>
      <div className="parallax">
        <h1 className='parallax__title'>Александр Машкин</h1>
        <p className="parallax__description">Архитектор-проектировщик</p>
      </div>
    </ParallaxComponent>
  );
};

export default Parallax;